import React, { useState, useTransition } from 'react';
import Layout from '../components/layout';
import InnerPageTitle from '../components/utils/inner-page-title';
import signs from '../assets/contracts/signatures/cshirts-privatesale.json';
import { ethers } from "ethers";
import SaleType from '../model/saleType';
import Constants from '../utils/constants';
import { Deferrable } from 'ethers/lib/utils';
import loading from '../assets/images/loading.gif';
import {Link} from 'gatsby';

declare var window: any;
declare var provider: any;

if(typeof window !== 'undefined')
{
   provider =  new ethers.providers.Web3Provider(window.ethereum);
}

const MintPage = () => {     
    const now = new Date();
    const [maxPerWallet, setMaxPerWallet] = useState(0);
    const [currentSaleType, setCurrentSaleType] = useState(SaleType.NotStarted);
    const [currentMainSaleType, setCurrentMainSaleType] = useState(SaleType.Private);
    const [walletAddress, setWalletAddress] = useState('');
    const [isConnected, setIsConnected] = useState(false);
    const [connectionMessage, setConnectionMessage] = useState('');
    const [numebrToMint, setNumberToMint] = useState(1);
    const [signer, setSigner] = useState();
    const [isMinting, setIsMinting] = useState(false);
    const [mintingSuccess, setMintingSuccess] = useState(false);
    const [alreadyMinted, setAlreadyMinted] = useState(0);
    const [mintingTransaction, setMintingTransaction] = useState('');
    const [mintError, setMintError] = useState('');
    const [totalSupply, setTotalSupply] = React.useState(0); 
    const [price, setPrice] = React.useState(''); 

    React.useEffect(() => {
        async function updateAlreadyMinted() {
            const providerAlchemy = new ethers.providers.JsonRpcProvider(Constants.ALCHEMY_URL);
            const publicContract = new ethers.Contract(Constants.CSHIRTS_CONTRACTADDRESS, Constants.CSHIRTS_CONTRACTABI, providerAlchemy);     
            const currentAlreadyMinted = await publicContract.totalSupply();
            setAlreadyMinted(ethers.BigNumber.from(currentAlreadyMinted).toNumber());          
        };

        async function getContractData() {
            const providerAlchemy = new ethers.providers.JsonRpcProvider(Constants.ALCHEMY_URL);
            const publicContract = new ethers.Contract(Constants.CSHIRTS_CONTRACTADDRESS, Constants.CSHIRTS_CONTRACTABI, providerAlchemy); 

            Promise.all([
                publicContract.MAX_SUPPLY(),
                publicContract.totalSupply(),
                publicContract._price(),
                publicContract._stage() // stage of the contract. type is int: {0 = PAUSED | 1 = ALLOWLIST | 2 = WAITLIST}    
            ]).then(function(values) {
                console.log(values);
                setTotalSupply(ethers.BigNumber.from(values[0]).toNumber());
                setAlreadyMinted(ethers.BigNumber.from(values[1]).toNumber());
                setPrice(ethers.utils.formatEther(ethers.BigNumber.from(values[2]).toString()));
                setCurrentMainSaleType(now < Constants.CSHIRTS_PUBLICSALE_DATE ? SaleType.Private : SaleType.Public);
                if(values[3] == 2) {
                    setCurrentSaleType(SaleType.Private);
                    setCurrentMainSaleType(SaleType.Private);
                    setMaxPerWallet(Constants.CSHIRTS_PRIVATESALE_MAXPERWALLET);
                } else if(values[3] == 1) {
                    setCurrentSaleType(SaleType.Public);
                    setCurrentMainSaleType(SaleType.Public);
                    setMaxPerWallet(Constants.CSHIRTS_PUBLICSALE_MAXPERWALLET);
                } else if(values[3] == 0) {                    
                    if(now < Constants.CSHIRTS_PRIVATESALE_DATE) {
                        setCurrentSaleType(SaleType.NotStarted);
                    }
                    else {
                        setCurrentSaleType(SaleType.Pause);
                    }
                }
            });
        }

        getContractData();
        setInterval(() => {
            updateAlreadyMinted();
          }, process.env.NODE_ENV === "development" ? 15000 : 5000);
    }, []);

    window.ethereum.on("chainChanged", (chainId:any) => {   
        window.location.reload();
    });

    const connectWallet = (async() => {
        if(window.ethereum) {           
            try {
                setConnectionMessage('');
                await provider.send('eth_requestAccounts', []);
                var currentChainID = await provider.getNetwork();
                if(currentChainID.chainId == Constants.CHAINID) {
                    const currentSigner = provider.getSigner();
                    setSigner(currentSigner);
                    const address = await currentSigner.getAddress();
                    if(address) {
                        setWalletAddress(address);
                        setIsConnected(true);    
                    }    
                    else { setWalletAddress(''); };
                }
                else { setConnectionMessage("Please select the " + Constants.CHAIN_NAME + " network in your wallet"); }
            } 
            catch(err) 
            { 
                setConnectionMessage(err.message); 
            }  
        } 
        else { setConnectionMessage("Please install Metamask"); };
    });

    const incremenetNumber = () => {
        if(numebrToMint < maxPerWallet) {
            setNumberToMint(numebrToMint + 1);
        }
    };

    const decremenetNumber = () => {
        if(numebrToMint > 1) {
            setNumberToMint(numebrToMint - 1);
        }
    };

    const onValueChange = () =>{};

    const mint =  (async() => {        
        if(window.ethereum && isConnected && walletAddress && alreadyMinted < totalSupply) {
            var signature = signs[walletAddress];
            var contract = new ethers.Contract(Constants.CSHIRTS_CONTRACTADDRESS, Constants.CSHIRTS_CONTRACTABI, signer); 
            try {
                var nftTx;
                if(currentSaleType == SaleType.Private) {
                    nftTx = await contract.mintWaitlist(signature, numebrToMint, { value: ethers.utils.parseEther(price).mul(numebrToMint), gasLimit: 10000000 });                
                }   
                if(currentSaleType == SaleType.Public) {
                    nftTx = await contract.mint(numebrToMint, { value: ethers.utils.parseEther(price).mul(numebrToMint), gasLimit: 10000000 });
                }  
                if(nftTx) {            
                    setMintingTransaction(nftTx.hash);
                    setIsMinting(true);        
                    try {
                        var receipt = await nftTx.wait();
                        if(receipt.status == 1) {
                            setMintingSuccess(true);
                            setIsMinting(false);
                        }
                    }
                    catch(txError) {                        
                        try {
                            const a = await provider.getTransaction(nftTx.hash);
                            let code = await provider.call(a as Deferrable<ethers.providers.TransactionRequest>, a.blockNumber)
                        } catch (txInternalError) {
                            console.log({txInternalError});
                            setMintError(txInternalError.reason);
                            setIsMinting(false);
                            setMintingSuccess(false);

                            /* const code = err.data.replace('Reverted ','');                            
                            let reason = ethers.utils.toUtf8String('0x' + code.substr(138)); */
                        }
                    }
                    const alreadyMintedUpdated = await contract.totalSupply();
                    setAlreadyMinted(ethers.BigNumber.from(alreadyMintedUpdated).toNumber());                    
                }
            }
            catch(error) {
                setMintingSuccess(false);
                setIsMinting(false);
                setMintError(error.message);
            }
        };
    });

    const PageLayout = <Layout pageTitle="Non Fungible Cup: Mint" container="inner-page">
        <InnerPageTitle title="Mint your C-Shirts" subtitle="Connect your wallet, mint your NFTs and start playing" />
        <section className="tf-section tf-item tf-mint">
            <div className="container">
                <div className={isConnected ? "hide" : "row"}>
                    <div className="col-md-12 center">
                        <a href="#" className={isConnected ? "hide" : "tf-button btn-effect"} onClick={connectWallet}>
                            <span className="boder-fade"></span>          
                            <span className="effect">Connect your wallet</span>
                        </a><br />
                        <span><strong>{connectionMessage}</strong></span><br />
                    </div>
                </div>  
                <div className={isConnected ? "row" : "hide"}>
                    <div className="col-md-12 center">
                        <h4>{currentMainSaleType == SaleType.Public ? "Public sale" : "Private sale"}</h4>
                        <div className={isConnected ? "wallet" : "hide"}>Wallet connected: {walletAddress}</div>                        
                        <div className={isConnected && alreadyMinted < totalSupply ? "mint-container" : "hide"}>
                            <div className={currentSaleType == SaleType.Pause ? "mint-container toUpper" : "hide"}>
                                Mint paused
                                <div>Minted {alreadyMinted} of {totalSupply}</div>
                            </div>
                            <div className={currentSaleType == SaleType.NotStarted ? "mint-container toUpper" : "hide"}>
                                Mint will start at {currentSaleType == SaleType.Private ? Constants.CSHIRTS_PRIVATESALE_DATE.toUTCString() : Constants.CSHIRTS_PUBLICSALE_DATE.toUTCString()}
                            </div>
                            <div className={currentSaleType == SaleType.Private || currentSaleType == SaleType.Public ? "mint-container toUpper" : "hide"}>
                                With your C-Shirts you can free mint tournaments NFTs and start earning ETH for every match win
                                <div>Minted {alreadyMinted} of {totalSupply}</div>
                            </div>
                            <div>
                                Price: {price}
                            </div>
                            <div className={currentSaleType == SaleType.Private || currentSaleType == SaleType.Public ? "d-flex justify-content-center" : "hide"}>
                                <div className="d-flex flex-row">
                                    <div className="p-2">
                                        <button onClick={decremenetNumber} disabled={numebrToMint == 0} className="increment-decrement">-</button>
                                    </div>
                                    <div className="p-2">
                                        <input type="text" value={numebrToMint} onChange={onValueChange}></input>
                                    </div>
                                    <div className="p-2">
                                        <button onClick={incremenetNumber} disabled={numebrToMint == maxPerWallet} className="increment-decrement">+</button>
                                    </div>
                                </div>
                            </div>
                            <div className={currentSaleType == SaleType.Private || currentSaleType == SaleType.Public ? "mint-button" : "hide"}>
                                <button className={isConnected ? "tf-button btn-effect" : "hide"} onClick={mint} disabled={isMinting}>
                                    <span className="boder-fade"></span>          
                                    <span className="effect">MINT</span>
                                </button> 
                                <div className={isMinting ? 'mint-messages' : 'hide'}>                                    
                                    Mint in progress ... <a href={Constants.SCAN_URL + mintingTransaction} title="See transaction" target="_blank">See transaction</a><br />
                                    <img src={loading} />                                    
                                </div>
                                <div className={mintingSuccess ? 'mint-messages' : 'hide'}>
                                    Mint succeeded! <a href={Constants.SCAN_URL + mintingTransaction} title="See transaction" target="_blank">See transaction</a>
                                </div>
                                <div className={mintError ? 'mint-error mint-messages': 'hide'}>
                                    Something went wrong: {mintError}
                                </div>
                            </div>
                        </div>
                        <div className={alreadyMinted >= totalSupply && currentSaleType == SaleType.Public ? "mint-container" : "hide"}>
                            COLLECTION MINTED OUT
                        </div>
                        <div className={alreadyMinted >= totalSupply && currentSaleType == SaleType.Private ? "mint-container" : "hide"}>
                            PRIVATE SALE MINTED OUT<br />
                            Public sales start on {Constants.CSHIRTS_PUBLICSALE_PRICE.toLocaleString()}
                        </div>
                    </div>
                </div>              
            </div>
        </section>
        <section className="tf-section tf-item tf-mint">
            <div className="container">
                <div className="row">
                    <div className="col-md-12 center subtitle">
                        <h5>Mint a C-Shirt and have a free-mint for the next drops</h5>
                        <p className="sub-title">Only C-Shirts holders can have a free-mint on every tournament</p>
                    </div>
                </div>
                <div className="row next-mints">
                    <div className="col-md-4 center">
                        <Link to="/tournaments/nfl22-23">
                            <div className="box-text corner-box">
                                <div><strong>NFL 2022/23</strong></div>
                                <p className="alignLeft">
                                    Date: Sept 2, 2022 - 10:00 UTC<br />
                                    Total supply: TBD<br />
                                    Private sale: free-mint<br />
                                    Public sale: 1 ETH<br />
                                    Gamification: 60% of sold (in ETH)
                                </p>
                            </div>    
                        </Link>                    
                    </div>
                    <div className="col-md-4 center">
                        <Link to="/tournaments/uechampionsleague22-23">
                            <div className="box-text corner-box">
                                <div><strong>UEFA Champions League 2022/23</strong></div>
                                <p className="alignLeft">
                                    Date: TBD<br />
                                    Total supply: TBD<br />
                                    Private sale: free-mint<br />
                                    Public sale: 1 ETH<br />
                                    Gamification: 60% of sold (in ETH)
                                </p>
                            </div>  
                        </Link>
                    </div>
                    <div className="col-md-4 center">
                        <Link to="/tournaments/nba22-23">
                            <div className="box-text corner-box">
                                <div><strong>NBA 2022/23</strong></div>
                                <p className="alignLeft">
                                    Date: TBD<br />
                                    Total supply: TBD<br />
                                    Private sale: free-mint<br />
                                    Public sale: 1 ETH<br />
                                    Gamification: 60% of sold (in ETH)
                                </p>
                            </div>  
                        </Link>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-4 center">
                        <Link to="/tournaments/nhl22-23">
                            <div className="box-text corner-box">
                                <div><strong>NHL 2022/23</strong></div>
                                <p className="alignLeft">
                                    Date: TBD<br />
                                    Total supply: TBD<br />
                                    Private sale: free-mint<br />
                                    Public sale: 1 ETH<br />
                                    Gamification: 60% of sold (in ETH)
                                </p>
                            </div>  
                        </Link>
                    </div>
                    <div className="col-md-4 center">
                        <Link to="/tournaments/worldcupqatar22">
                            <div className="box-text corner-box">
                                <div><strong>World Cup Qatar 2022</strong></div>
                                <p className="alignLeft">
                                    Date: TBD<br />
                                    Total supply: TBD<br />
                                    Private sale: free-mint<br />
                                    Public sale: 1 ETH<br />
                                    Gamification: 60% of sold (in ETH)
                                </p>
                            </div>  
                        </Link>
                    </div>
                    <div className="col-md-4 center">

                    </div>
                </div>
            </div>
        </section>
    </Layout>
    return(
        PageLayout
    );
};

export default MintPage;